import React, { useState, getState, useEffect, useCallback } from 'react'
import { Grid, Typography } from '@material-ui/core'
import { Module, List, Setting } from './components'
import { sortBy, useToggle, useInput, useTitle } from './util/treeUtil'
import { debounce } from "lodash";
import { getVideos } from '../../../api/VideoApi';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';

/**
 * From React Showcases:
 * https://tree.valleyease.me/
 * https://github.com/ValleyZw/tree
 */

const moduleLayout = {
  xs: 12,
  sm: 6,
  md: 4,
  lg: 3,
  xl: 3
}

const listLayout = {
  xs: 12,
  sm: 12,
  md: 12,
  lg: 6,
  xl: 6
}

export default function VideoList(props) {
  const [sortValue, setLSortValue] = useState('')
  const { toggle, setToggle } = useToggle(false)
  const [searchValue, setSearchValue] = useInput()

  const [videos, setVideos] = useState(props.videos)
  const [totalCount, setTotalCount] = useState(props.totalCount)

  const [offset, setOffset] = useState(0)
  const [pageLimit, setPageLimit] = useState(12)

  useTitle('Video List');

  const layout = toggle ? listLayout : moduleLayout
  const sortedVideos = sortBy(videos.filter((p) => p.attributes.source.toLowerCase().includes(searchValue.toLowerCase())), (a, b) => b[sortValue] - a[sortValue])

  const handler = useCallback(debounce(getVideos, 600), []);

  useEffect(() => {
    const delayDebounceFn = setTimeout(() => {
      console.log("debounce... ", searchValue);
      setOffset(0);
      handler(updateVideoList, searchValue, 0, pageLimit);
    }, 500)

    return () => clearTimeout(delayDebounceFn)
  }, [searchValue])

  const updateSearchValue = (newValue) => {
    setSearchValue(newValue);
  }

  const updateVideoList = (data, count) => {
    setVideos(data);
    setTotalCount(count);
    const maxOffset = (Math.floor(count / pageLimit) - 1) * pageLimit;
    if (offset > maxOffset) {
      setOffset(maxOffset);
    }
  }

  const refresh = (searchValue, limit, page) => {
    setPageLimit(limit)
    handler(updateVideoList, searchValue, offset, limit);
  }

  const pageChange = (newOffset) => {
    const maxOffset = (Math.floor(totalCount / pageLimit) - 1) * pageLimit;
    console.log(maxOffset);
    if (newOffset < 0) {
      newOffset = maxOffset;
    } else if (newOffset > maxOffset) {
      newOffset = 0;
    }
    setOffset(newOffset);
    handler(updateVideoList, searchValue, newOffset, pageLimit);
  }

  const onBack = () => {
    pageChange(offset - pageLimit)
  }

  const onForward = () => {
    pageChange(offset + pageLimit)
  }

  return (
    <>
      <Tooltip title="Back">
        <IconButton aria-label="back" onClick={onBack} >
          <ArrowBackIosIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Next">
        <IconButton aria-label="Next" onClick={onForward} >
          <ArrowForwardIosIcon />
        </IconButton>
      </Tooltip>
      <Typography>{ Math.floor(offset / pageLimit) + 1 } / { Math.floor(totalCount / pageLimit) }</Typography>
      <Setting {...getState} searchValue={searchValue} setSearchValue={updateSearchValue} setLSortValue={setLSortValue} toggle={toggle} setToggle={setToggle} bubbleUp={refresh} />
      <Grid container spacing={4}>
        {sortedVideos.map((video) => (
          <Grid item key={video.id} {...layout}>
            {toggle ? <List {...video} /> : <Module {...video} />}
          </Grid>
        ))}
      </Grid>
    </>
  )
}
